@-webkit-keyframes hike {
0% {
    -webkit-transform: translate(0, 0) scale(1);
            transform: translate(0, 0) scale(1);
}
33% {
    -webkit-transform: translate(20%, -30%) scale(0.8);
            transform: translate(20%, -30%) scale(0.8);
}
66% {
    -webkit-transform: translate(20%, -40%) scale(0.6);
            transform: translate(20%, -40%) scale(0.6);
}
100% {
    -webkit-transform: translate(5%, -50%) scale(0.4);
            transform: translate(5%, -50%) scale(0.4);
}
}
@keyframes hike {
0% {
    -webkit-transform: translate(0, 0) scale(1);
            transform: translate(0, 0) scale(1);
}
33% {
    -webkit-transform: translate(20%, -30%) scale(0.8);
            transform: translate(20%, -30%) scale(0.8);
}
66% {
    -webkit-transform: translate(20%, -40%) scale(0.6);
            transform: translate(20%, -40%) scale(0.6);
}
100% {
    -webkit-transform: translate(5%, -50%) scale(0.4);
            transform: translate(5%, -50%) scale(0.4);
}
}
@-webkit-keyframes rockfall {
0% {
    -webkit-transform: translate(0, 0) rotateZ(0);
            transform: translate(0, 0) rotateZ(0);
}
50% {
    -webkit-transform: translate(2%, 20%) rotateZ(10deg);
            transform: translate(2%, 20%) rotateZ(10deg);
}
100% {
    -webkit-transform: translate(10%, 63%) rotateZ(20deg);
            transform: translate(10%, 63%) rotateZ(20deg);
}
}
@keyframes rockfall {
0% {
    -webkit-transform: translate(0, 0) rotateZ(0);
            transform: translate(0, 0) rotateZ(0);
}
50% {
    -webkit-transform: translate(2%, 20%) rotateZ(10deg);
            transform: translate(2%, 20%) rotateZ(10deg);
}
100% {
    -webkit-transform: translate(10%, 63%) rotateZ(20deg);
            transform: translate(10%, 63%) rotateZ(20deg);
}
}
@-webkit-keyframes drift {
0% {
    -webkit-transform: translateX(0) translateY(0) rotateZ(0);
            transform: translateX(0) translateY(0) rotateZ(0);
    opacity: 1;
}
100% {
    -webkit-transform: translateX(-65%) translateY(-20%) rotateZ(40deg);
            transform: translateX(-65%) translateY(-20%) rotateZ(40deg);
    opacity: 0;
}
}
@keyframes drift {
0% {
    -webkit-transform: translateX(0) translateY(0) rotateZ(0);
            transform: translateX(0) translateY(0) rotateZ(0);
    opacity: 1;
}
100% {
    -webkit-transform: translateX(-65%) translateY(-20%) rotateZ(40deg);
            transform: translateX(-65%) translateY(-20%) rotateZ(40deg);
    opacity: 0;
}
}
@-webkit-keyframes colorRocks {
0% {
    background-color: #7D7771;
}
100% {
    background-color: #3E3D3D;
}
}
@keyframes colorRocks {
0% {
    background-color: #7D7771;
}
100% {
    background-color: #3E3D3D;
}
}
